.main {
  width: 100%;
  
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 50px;
  .container{
    max-width: 700px;
    margin-left: 32px;
    p{
      font-size: 18px;
    }
  }
  .heading {
    font-size: 30px;
    font-weight: 900;
    border-left: 12px solid #fa163f;
    padding-left: 20px;
  }
  h3 {
    margin: 25px 0px;
    font-size: 20px;
    span{
        color: #fa163f
    };
  }
  a{
    text-decoration: none;
  }
  .button {
    border-radius: 0;
    text-transform: none;
    width: 100%;
   
    height: 100%;
    max-height: 50px;
    margin-top: 32px;
    font-weight: 600;
    font-size: 20px;
    background-color: #fa163f;
      color: white;
    
    &:hover {
      background-color: transparent;
      color: #0c0c0c;
      outline: 4px solid #fa163f;
    }
  }

  .flex {
    position: relative;
    .personalImage{
      position: absolute;
      width: 100%;
      left: 71%;
      top: -17%;
      max-width: 300px;
    }
    .contactContainer {
      display: flex;
      align-items: center;
      gap: 32px;
      margin: 32px 0px;
      width: 100%;
      
      .contact {
        width: 100%;
       
        padding: 10px;
        height: 100px;
        background-color: #e5e5e5;
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        flex-direction: column;
        p{
          font-weight: 800;
          color: #505050;
        }
        &:hover{
            cursor: copy;
            background-color: #b9b9b9;
        }
        &:active{
            background-color: #fa164063;
        }
      }
    }
    img {
      max-width: 30px;
    }
  }
  img {
    width: 100%;
    max-width: 428px;
    margin-right: 32px;
  }
}
